.englishCv {
    margin : 2em 0;
    display: flex;
    align-items: center;
    justify-content: center;
    a{
        margin: 3em;
        position: relative;
        display: block;
    }
    .active{
        &::after{
            content: "";
            width: 85%;
            height: 0.25em;
            background: $color-1;
            position: absolute;
            bottom: -0.5em;
            left: 40%;
            transform: translateX(-50%);
        }
    }
    .info_eng .userName{
        padding: 3em 0em 1em;
    }
    .info_eng .shortInfo{
        padding: 0em 0em 1em;
    }
    .info_eng .infoUser{
        padding: 0em 0em 1em;
    }
    .info_eng .infoLanguage{
        padding: 0em 0em 1em;
    }
    .info_eng .interests{
        padding: 0em 0em 1em;
    }
    .info_eng .reading{
        padding: 0em 0em 1em;
    }
    .info_eng .globalSkills{
        padding: 0em 0em 1em;
    }
    .info_eng .projectExample{
        padding: 0em 0em 1em;
    }
}